@charset "utf-8";

.advantage_top{
    display: flex;
    align-items: end;
    padding: 140px 0;
    gap: 100px;
    justify-content: space-between;
    width: 90.264vw;
}
.sec_title{
    margin-top: 40px;
}
.advantage_text{
    width: 40vw;
    min-width: 400px;
    line-height: 2.5;
}
.advantage_top_img{
    width: 51.2vw;
    max-width: 750px;
    min-width: 410px;
    overflow: hidden;
}
.advantage_flex{
    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;
}
.warm,.relaxed{
    width: 525px;
}
.interior_color{
    display: flex;
    align-items: end;
    gap: 40px;
}
.interior_color img{
    width: 285px;
}
.interior_text{
    line-height: 2;
}

.color_title{
    text-align: center;
    padding: 3px 0;
    width: 200px;
    border: solid 1px#000;
}
.color_scheme{
    text-align: center;
    font-size: 20px;
    margin: 20px 0 10px;
    letter-spacing: 2px;
}
.color_box{
    color: #fff;
    padding: 40px 10px 10px;
    font-size: 16px;
    font-weight: 100;
    position: relative;
    line-height: 1.2;
}
.warm_color1{
    background-color: #AD9A88;
    margin-bottom: 10px;
}
.warm_color2{
    background-color: #C7A494;
    width: 60%;
}
.relaxed_color1{
    background-color: #4D5357;
    margin-bottom: 10px;
}
.relaxed_color2{
    background-color: #43403B;
    width: 60%;
}
.sofa_color{
    background-color: #D8D7D6;
    width: 40%;
}
.sofa_color p{
    position: absolute;
    bottom: 10px;
    left: 10px;
}
.color_flex{
    display: flex;
    gap: 10px;
}
.interior_story{
    font-size: 20px;
    padding: 40px 0 20px;
    letter-spacing: 5px;
}